.cartClass {
  padding-top: 93px;
  padding-bottom: 100px;
  background-image: url(../img/backgrounds/clientarea.jpg);
  background-repeat: repeat-y;
  background-size: 100%;

  h3 {
    font-family: $futura;
    font-size: 24px;
    color: $colorHover;
    letter-spacing: -0.03px;
    text-align: center;
    text-transform: uppercase;
    line-height: 1.4;
  }
}


.undoStatic {
  width: 40px;
  height: 60px;
  background-image: url(../img/icons/undo.svg);
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: 0 center;
  margin: 10px 0;

  &:active {
    background-size: 15px;
  }
}

.productsList {
  padding: 20px 0;
}

.cartItem {
  display: flex;
  margin-bottom: 40px;

  img {
    width: 125px;
    background: #FDF9F6;
    box-shadow: 0 9px 11px 0 #DDCBB7; 
  }
  .description {
    padding-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;


    a {
      font-family: $futura;
      font-size: 18px;
      color: $color;
      letter-spacing: -0.02px;
      line-height: 1.1;
    }
  }
  .price {
    font-family: $futura;
    font-size: 18px;
    color: $color;
    text-transform: uppercase;
    margin: 0;
  }
  .params {
    font-family: $futura;
    font-size: 16px;
    color: $color;

    span:first-child {
      margin-right: 15px;
    }
  }
  .methods {
    display: flex;
    align-items: center;
    font-family: $futura;
    font-size: 18px;
    color: $color;

    .edit {
      margin-right: 15px;
      text-decoration: underline;
      position: relative;
    }
    .addToWish {
      padding-top: 3px;
    }
    select {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
      font-family: $futura;
      font-size: 18px;
      color: $color;
    }
  }
  .remove {
    width: 30px;
    height: 30px;
    background-image: url(../img/icons/plusIconBlack.svg);
    background-size: 25px;
    background-repeat: no-repeat;
    background-position: center center;
    margin-right: 0;
    margin-left: auto;
    transform: rotate(45deg);

    &:active {
      background-size: 20px;
    }
  }
}

.orderSummary{
  

  & > div {
    padding: 20px 15px;
    background-image: url(..//..backgrounds/filter.jpg);
    background-size: 100%;
    background-repeat: repeat-y;
    background-color: #FFF2EC;
    box-shadow: 0 3px 2px 0 #95908B;

    &:last-child {
      margin-top: 40px;
    }
  }
  .title {
    font-family: $futura;
    font-size: 24px;
    color: #B22D00;
    letter-spacing: 0;
    text-transform: uppercase;
  }
  span, label, button, p, a {
    font-family: $futura;
    font-size: 16px;
    color: #42261D;
    letter-spacing: 0;
    line-height: 30px;
  }
  label {
    line-height: 1;
  }
  .bloc {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  input, .selectContainer {
    width: 60%;
  }
  .promoCode {
    margin: 10px 0;

    button {
      background-image: url(../img/backgrounds/buttons.jpg);
      background-size: 100% 100%;
      height: 30px;
      border: 0;
      width: 35%;
      text-transform: uppercase;
      color: #B22D00;

      &:active {
        font-size: 14px;
      }
    }
    .error {
      display: none;
    }
  }
  .total span {
    font-family: $futura;
    font-size: 18px;
    color: #42261D;
    letter-spacing: 0;
    line-height: 40px;
  }
  .last span {
    font-family: $futura;
    font-size: 24px;
    color: #42261D;
    letter-spacing: 0;
    text-align: right;
    line-height: 2;
  }
  button.onSubmit {
    background-color: #FFE7CB;
    background-image: url(../img/backgrounds/onSubmit.jpg);
    background-size: 100% 100%;
    font-family: $futura;
    font-size: 18px;
    color: #42261D;
    text-align: center;
    text-transform: uppercase;
    color: #B22D00;
    width: 100%;
    height: 58px;
    line-height: 58px;
    border: 1px solid #FFFFFF;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.50);
    margin-top: 5px;
    
    &:active {
      font-size: 16px;
    }
  }
  p {
    font-size: 18px;
  }
  a {
    text-decoration: underline;
    font-size: 18px;
  }
}

.selectContainer {
  position: relative;
  width: 100%;
  border: 1px solid #42261D;
  margin: 5px 0;

  select {
    -webkit-appearance: none;
    -moz-appearance:    none;
    appearance:         none;
    background: none;
    border: 0;
    cursor: pointer;
    font-family: $futura, Georgia, 'Times New Roman', Times, serif;
    font-size: 18px;
    color: #42261D;
    letter-spacing: 0;
    line-height: 30px;
    height: 30px;
    padding-left: 15px;
    width: 100%;

    &:focus {
      outline: 0;
    }
  }
  span {
    position: absolute;
    right: 15px;
  }
}

input, textarea {
  font-family: $futura, Georgia, 'Times New Roman', Times, serif;
  background: none;
  outline: 0;
  font-size: 16px;
  line-height: 30px;
  height: 30px;
  border: 1px solid #42261D;
  width: 100%;
  padding-left: 15px;
}

.stepContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px 0;

  .step {
    position: relative;
    width: 40px;
    height: 40px;
    background-image: url(../img/backgrounds/buttons.jpg);
    background-size: cover;
    background-position: 100%;
    transform: rotate(45deg);
  }
  .step.opac {
    opacity: .5;

    .cat {
      display: none;
    }
  }
  span {
    font-family: $futura;
    font-size: 18px;
    color: #42261D;
    display: block;
    transform: rotate(-45deg);
    text-align: center;
    line-height: 40px;
  }
  .cat {
    background-image: url(../img/icons/cat.png);
    background-repeat: no-repeat;
    background-position: 100%;
    background-size: 100%;
    width: 70px;
    height: 87px;
    position: absolute;
    right: -32px;
    top: -40px;
    transform: rotate(-35deg);
  }
  .arrow {
    background-image: url(../img/icons/largeArrow.svg);
    background-size: 100%;
    background-repeat: no-repeat;
    width: 15%;
    height: 16px;
    margin: 0 20px;
    margin-top: 7px;
  }
}

.shippingInformation {

  .subTitle {
    font-family: $futura;
    font-size: 18px;
    color: #42261D;
    letter-spacing: 0;
    text-transform: uppercase;
    border-bottom: 1px solid #979797;
    line-height: 1.2;
    margin-bottom: 30px;
  }
  input {
    height: 44px;
    line-height: 44px;
    margin-bottom: 20px;
  }
  label {
    line-height: 1;
  }
  .shippingBloc {
    margin-bottom: 40px;
  }
  .butt {
    height: 44px;
    line-height: 44px;
    width: 225px;
    font-family: $futura;
    font-size: 18px;
    color: #B22D00;
    text-align: center;
    text-transform: uppercase;
    padding-top: 3px;
    margin-top: 10px;
  }
  .paymentMethod {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FFE8DF;
    outline: 1px solid #42261D;
    padding: 5px 0;
    height: 100%;
    width: 100%;

    &.selected {
      background-image: url(../img/backgrounds/paymentMethod.jpg);
      background-size: 100% 100%;
      outline: 0;
    }
    img {
      margin-bottom: 5px;
    }
    p {
      font-family: $futura;
      font-size: 14px;
      color: #42261D;
      letter-spacing: 0;
      text-align: center;
      margin-bottom: 5px;
    }
    span {
      font-family: $futura;
      font-size: 12px;
      color: rgba(66,38,29,0.50);
      letter-spacing: 0;
      text-align: center;
      line-height: 12px;
    }
    span img {
      display: inline;
    }
    &:first-child img{
      width: 49px;
    }
    &:nth-child(2) span {
      img {
        width: 70px;
      }
    }
  }
  .paymentMethod.ideal img {
    width: 30px;
  }
  .paymentMethod.visa p {
    margin-bottom: 15px;
  }
  .paymentMethodContainer {
    .checkContainer {
      font-family: $futura;
      font-size: 16px;
      color: #42261D;
      letter-spacing: 0;
      margin-left: 15px;
      margin-top: 20px;
      .check {
        transform: rotate(45deg);
        border: 1px solid #500504;
      }
    }
    &>div {
      padding: 3px;

      &:first-child {
        padding-left: 15px;
      }
      &:nth-child(3) {
        padding-right: 15px;
      }
    }
  }
}

.titleCeck {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  p {
    font-family: $futura;
    font-size: 20px;
    color: #42261D;
    letter-spacing: 0;
    text-transform: uppercase;
  }
  span {
    font-family: $futura;
    font-size: 14px;
    color: #4C493F;
    letter-spacing: 0;
  }
}

.info {
  margin-top: 60px;
  padding-left: 15px;

  p.title {
    font-family: $futura;
    font-size: 24px;
    color: #42261D;
  }
  .paymentMethods {

    li {
      margin-right: 30px;
    }
  }
  ul {
    display: flex;
    align-items: center;
  }
  .infoBloc {
    margin-bottom: 30px;
  }
}


